import React, { Component } from "react";
import PropTypes from "prop-types";

import { Alert, Container, Row, Col, Card, CardBody, CardTitle } from "reactstrap";

// availity-reactstrap-validation
import { AvField, AvForm } from "availity-reactstrap-validation";

// Redux
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";

import AceEditor from "react-ace";
import "ace-builds/src-noconflict/mode-css";
import "ace-builds/src-noconflict/mode-json";
import "ace-builds/webpack-resolver";

//Import Breadcrumb
import Breadcrumbs from "../../../components/Common/Breadcrumb";

import { getClient, editClient } from "../../../store/actions";

class Configuration extends Component {
  constructor(props) {
    super(props);
    this.state = {
      id: '',
      name: '',
      wizard: '',
      css: ''
    };

    this.handleValidSubmit = this.handleValidSubmit.bind(this);
  }

  componentDidMount() {
    this.props.getClient({ id: this.props.match.params.id, callback: (result) => this.onClientSuccess(result) });
  }

  onClientSuccess(success) {
    if (success) {
      this.setState({
        id: this.props.client.id,
        name: this.props.client.name,
        wizard: this.props.client.wizard,
        css: this.props.client.css
      });
    }
  }

  handleValidSubmit(event, values) {
    values.wizard = this.state.wizard;
    values.css = this.state.css;

    this.props.editClient(values, (result) => this.onClientEdit(result, values));
  }

  onClientEdit(result, values) {
    if (result) {
      this.setState({
        id: values.id,
        name: values.name,
        wizard: values.wizard,
        css: values.css
      });
    }

    window.scrollTo(0, 0);
  }

  render() {
    return (
      <React.Fragment>
        <div className="page-content">
          <Container fluid={true}>
            <Breadcrumbs title="Clientes" breadcrumbItem="Detalles" />
            { this.props.loading ? (
              <div className="spinner-border text-primary m-1" role="status">
                <span className="sr-only">Cargando...</span>
              </div>
            ) : (
            <AvForm
              className="form-horizontal"
              onValidSubmit={(e, v) => {
                this.handleValidSubmit(e, v)
              }}
            >
              {this.props.updated ? (
                <Alert color="success">
                  Datos guardados correctamente.
                </Alert>
              ) : null}
              {this.props.error ? (
                <Alert color="danger">
                  {this.props.error}
                </Alert>
              ) : null}
              <Row>
                <Col>
                  <Card>
                    <CardBody>
                      <CardTitle className="h4">General</CardTitle>

                      <AvField
                        name="id"
                        value={this.state.id}
                        label="ID"
                        type="text"
                        placeholder="ID"
                        errorMessage=" "
                        className="mb-3"
                        readOnly
                        disabled
                      />

                      <AvField
                        name="name"
                        value={this.state.name}
                        label="Nombre"
                        type="text"
                        placeholder="Nombre"
                        errorMessage=" "
                        required
                      />
                    </CardBody>
                  </Card>
                </Col>
              </Row>

              <Row>
                <Col>
                  <Card>
                    <CardBody>
                      <CardTitle className="h4">JSON</CardTitle>

                      {!!this.props.client ? (
                        <AceEditor
                          mode="json"
                          onChange={(json) => { this.setState({ wizard: json }) }}
                          name="json"
                          width="100%"
                          value={this.state.wizard}
                          showPrintMargin={false}
                          setOptions={{
                            enableBasicAutocompletion: true,
                            enableLiveAutocompletion: true,
                            enableSnippets: true,
                            showLineNumbers: true,
                            tabSize: 2,
                          }}
                        />
                      ) : null}
                    </CardBody>
                  </Card>
                </Col>
              </Row>

              <Row>
                <Col>
                  <Card>
                    <CardBody>
                      <CardTitle className="h4">CSS</CardTitle>

                      {!!this.props.client ? (
                        <AceEditor
                          mode="css"
                          onChange={(css) => { this.setState({ css: css }) }}
                          name="css"
                          width="100%"
                          value={this.state.css}
                          showPrintMargin={false}
                          setOptions={{
                            enableBasicAutocompletion: true,
                            enableLiveAutocompletion: true,
                            enableSnippets: true,
                            showLineNumbers: true,
                            tabSize: 2,
                          }}
                        />
                      ) : null}
                    </CardBody>
                  </Card>
                </Col>
              </Row>
              <div className="d-grid justify-content-end mb-3">
                <button type="submit" className="btn btn-primary w-md">
                  Guardar
                </button>
              </div>
            </AvForm>
            )}
          </Container>
        </div>
      </React.Fragment>
    )
  }
}

Configuration.propTypes = {
  client: PropTypes.object,
  error: PropTypes.any,
  history: PropTypes.object,
};

const mapStateToProps = state => {
  const { loading, client, updated, error } = state.Clients;
  return {
    loading, client, updated, error
  };
};

export default connect(mapStateToProps, {
  getClient,
  editClient
})(withRouter(Configuration));