import React from "react";
import PropTypes from "prop-types";
import { Container, Row, Col, Card, CardBody, CardTitle, CardText, CardImg } from "reactstrap";
// Redux
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import Styled from "styled-components";
import Redirect from "../components/Redirect";
import Text from "../components/Text";
import Popup from "../components/Popup";

import { getWizard } from "../store/actions";

class Home extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      option: null,
      steps: [],
      styles: this.props.css ? this.props.css : ''
    };
  }

  componentDidMount() {
    this.props.getWizard({ id: this.props.match.params.id, callback: (result) => this.onWizardSuccess(result) });
  }

  onWizardSuccess(success) {
    if (success) {
      this.setState({
        steps: this.props.wizard.options,
        styles: this.props.css
      });
    }
  }

  renderComponent(step) {
    switch (step.type) {
      case 'redirect':
        return <Redirect step={step}></Redirect>;
      case 'text':
        return <Text step={step}></Text>;
      case 'html':
        return <Popup step={step}></Popup>;
    }
  }

  setOption(option) {
    let steps = this.state.steps.map((step) => {
      if (option.id == step.id) {
        step.styles = {
          borderColor: this.props.wizard.color,
          borderWidth: 3
        };
      } else {
        step.styles = undefined;
      }

      return step;
    });

    this.setState({ steps, option });
  }

  nextStep() {
    let steps = this.state.steps.map((step) => {
      step.styles = undefined;

      return step;
    });

    this.setState({ steps });

    this.props.history.push('/' + this.props.id + '/' + this.state.option.id);
  }

  Wrapper = () => Styled.div`
    ${this.state.styles}
  `;
  render() {
    const Wrapper = this.Wrapper();

    return (
      this.props.wizard ? (
      <Wrapper>
        <Container className="wizard-container">
          <Row className="justify-content-center mt-3 mb-3">
            <Col>
              <div className="text-center cursor-pointer">
                  <img src={this.props.wizard.logo} className="img-fluid" onClick={() => { this.props.history.push('/' + this.props.id) }} />
              </div>
            </Col>
          </Row>
          <Row className="justify-content-center text-center mb-3">
            <Col md={6} xl={3}>
              <h1>{this.props.wizard.title}</h1>
              <h5>{this.props.wizard.subtitle}</h5>
            </Col>
          </Row>
          <Row className="justify-content-center">
            {this.state.steps.map((step, key) => {
              return (
                <Col md={3} xs={6} key={step.id} className="mb-3">
                  {step.options && step.options.length ? (
                    <Card className="h-100 cursor-pointer" style={step.styles} onClick={() => { this.setOption(step) }}>
                      <CardImg top className="img-fluid" src={step.image} />
                      <CardBody>
                        <CardTitle className="h4 mt-0">{step.title}</CardTitle>
                        <CardText dangerouslySetInnerHTML={{ __html: step.content ? step.content.replace(/(<? *script)/gi, 'illegalscript') : '' }}>
                        </CardText>
                      </CardBody>
                    </Card>
                  ) : this.renderComponent(step)}
                </Col>
              );
            })}
            <Row className="justify-content-center text-center mt-3 actions">
              <Col md={3} xs={12} className="p-0">
                <button disabled={!this.state.option} onClick={() => this.nextStep() } className="btn">Continuar</button>
              </Col>
            </Row>
          </Row>
        </Container>
      </Wrapper>
      ) : null
    );
  }
}

Home.propTypes = {
  id: PropTypes.string,
  wizard: PropTypes.object,
  css: PropTypes.string,
  error: PropTypes.any,
  history: PropTypes.object,
};

const mapStateToProps = state => {
  const { loading, id, wizard, css, error } = state.Wizard;
  return {
    loading, id, wizard, css, error
  };
};

export default connect(mapStateToProps, {
  getWizard
})(withRouter(Home));