import React, { Component } from "react";
import PropTypes from "prop-types";

import { Container, Row, Col, Card, CardBody, CardTitle } from "reactstrap";
import { Table, Thead, Tbody, Tr, Th, Td } from "react-super-responsive-table";
import "react-super-responsive-table/dist/SuperResponsiveTableStyle.css";

// Redux
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";

import { getClients } from "../../../store/actions";

class Clients extends Component {
  constructor(props) {
    super(props);
    this.state = {
      clients: []
    };
  }

  componentDidMount() {
    this.props.getClients({
      callback: (result) => this.onClientsSuccess(result)
    });
  }

  onClientsSuccess(success) {
    if (success) {
      this.setState({
        clients: this.props.clients
      });
    }
  }

  render() {
    return (
      <React.Fragment>
        <div className="page-content">
          <Container fluid={true}>
            <Card>
              <CardBody>
                <CardTitle className="h4">Clientes</CardTitle>
                  <Row>
                    <Col lg="12">
                      <div className="table-rep-plugin">
                        <div
                          className="table-responsive mb-0"
                          data-pattern="priority-columns"
                        >
                          <Table className="table table-striped table-bordered">
                            <Thead>
                              <Tr>
                                <Th>ID</Th>
                                <Th>Nombre</Th>
                              </Tr>
                            </Thead>
                            <Tbody>
                              {this.state.clients.map((client) =>
                                <Tr className="cursor-pointer" key={client.id} onClick={() => { this.props.history.push("/admin/clients/" + client.id) }}>
                                  <Td>
                                    {client.id}
                                  </Td>
                                  <Td>
                                    {client.name}
                                  </Td>
                                </Tr>
                              )}
                              {!this.props.loading && !this.state.clients.length &&
                                <Tr>
                                  <Td colSpan="2">No existen clientes</Td>
                                </Tr>
                              }
                              {this.props.loading &&
                                <Tr>
                                  <Td colSpan="2">
                                    <div className="spinner-border text-primary m-1" role="status">
                                      <span className="sr-only">Cargando...</span>
                                    </div>
                                  </Td>
                                </Tr>
                              }
                            </Tbody>
                          </Table>
                        </div>
                      </div>
                    </Col>
                  </Row>
                </CardBody>
              </Card>
          </Container>
        </div>
      </React.Fragment>
    )
  }
}

Clients.propTypes = {
  clients: PropTypes.array,
  error: PropTypes.any,
  history: PropTypes.object,
};

const mapStateToProps = state => {
  const { loading, clients, error } = state.Clients;
  return {
    loading, clients, error
  };
};

export default connect(mapStateToProps, {
  getClients
})(withRouter(Clients));