import React, { Component } from "react";
import { BrowserRouter as Router, Switch } from "react-router-dom";

// Import Routes
import { authProtectedRoutes, publicRoutes } from "./routes/";
import AppRoute from "./routes/route";

// Import Firebase Configuration file
import { initFirebaseBackend } from "./helpers/firebase_helper";

// layouts
import NonAuthLayout from "./components/NonAuthLayout";
import HorizontalLayout from "./components/HorizontalLayout/";

import Home from "./pages/Home";
import Step from "./pages/Step";

// Import scss
import "./assets/scss/theme.scss";

// Activating Firebase
const firebaseConfig = {
  apiKey: process.env.REACT_APP_APIKEY,
  authDomain: process.env.REACT_APP_AUTHDOMAIN,
  databaseURL: process.env.REACT_APP_DATABASEURL,
  projectId: process.env.REACT_APP_PROJECTID,
  storageBucket: process.env.REACT_APP_STORAGEBUCKET,
  messagingSenderId: process.env.REACT_APP_MESSAGINGSENDERID,
  appId: process.env.REACT_APP_APPID,
  measurementId: process.env.REACT_APP_MEASUREMENTID,
};

// init firebase backend
initFirebaseBackend(firebaseConfig);

const getWizardRoutes = (array) => {
  var result = [];
  array.forEach(function (a) {
    result.push(a);
    if (Array.isArray(a.options)) {
      result = result.concat(getWizardRoutes(a.options));
    }
  });

  return result;
}

let wizardRoutes = [];
wizardRoutes = getWizardRoutes(window.WIZARD_CONFIG.options);
wizardRoutes.unshift({ path: "/:id", id: "home" });

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <React.Fragment>
        <Router>
          <Switch>
            {publicRoutes.map((route, idx) => (
              <AppRoute
                path={route.path}
                layout={NonAuthLayout}
                component={route.component}
                key={idx}
                isAuthProtected={false}
              />
            ))}

            {authProtectedRoutes.map((route, idx) => (
              <AppRoute
                path={route.path}
                layout={HorizontalLayout}
                component={route.component}
                key={idx}
                isAuthProtected={true}
                exact
              />
            ))}

            {wizardRoutes.map((step) => (
              <AppRoute
                path={step.id == "home" ? '/:id' : '/:id/' + step.id}
                layout={NonAuthLayout}
                component={step.id == "home" ? Home : Step}
                key={step.id}
                isAuthProtected={false}
                step={step}
                exact
              />
            ))}
          </Switch>
        </Router>
      </React.Fragment>
    )
  }
}

export default App;
