import React from "react";
import PropTypes from "prop-types";
import { Container, Row, Col, Card, CardBody, CardTitle, CardText, CardImg } from "reactstrap";
import Styled from "styled-components";
// Redux
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import { some } from "lodash";

import Redirect from "../components/Redirect";
import Text from "../components/Text";
import Popup from "../components/Popup";

import { getWizard } from "../store/actions";

class Step extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      option: null,
      step: props.step,
      styles: this.props.css ? this.props.css : ''
    };
  }

  componentDidMount() {
    this.props.getWizard({ id: this.props.match.params.id, callback: (result) => this.onWizardSuccess(result) });
  }

  onWizardSuccess(success) {
    if (success) {
      this.setState({
        steps: this.props.wizard.options,
        styles: this.props.css
      });
    }
  }

  renderComponent(step) {
    switch (step.type) {
      case 'redirect':
        return <Redirect step={step}></Redirect>;
      case 'text':
        return <Text step={step}></Text>;
      case 'html':
        return <Popup step={step}></Popup>;
    }
  }

  setOption(option) {
    let steps = this.state.step.options.map((step) => {
      if (option.id == step.id) {
        step.styles = {
          borderColor: this.props.wizard.color,
          borderWidth: 3
        };
      } else {
        step.styles = undefined;
      }

      return step;
    });

    this.setState({ steps, option });
  }

  nextStep() {
    let steps = this.state.step.options.map((step) => {
      step.styles = undefined;

      return step;
    });

    this.setState({ steps });

    this.props.history.push('/' + this.props.id + '/' + this.state.option.id);
  }

  hasAnySubSteps() {
    return some(this.state.step.options, (step) => {
      return step.options && step.options.length;
    });
  }

  Wrapper = () => Styled.div`
    ${this.state.styles}
  `;
  render() {
    const Wrapper = this.Wrapper();

    return (
      this.props.wizard ? (
      <Wrapper>
        <Container className="mb-3 wizard-container">
          <Row className="mt-3 mb-3">
            <Col>
              <div className="text-center cursor-pointer">
                <img src={this.props.wizard.logo} className="img-fluid" onClick={() => { this.props.history.push('/' + this.props.id) }} />
              </div>
            </Col>
          </Row>
          <Row className="justify-content-center text-center">
            <Col md={12} onClick={() => this.props.history.goBack()}>
              <div className="back-arrow">
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 64 64" aria-labelledby="title"
                  aria-describedby="desc" role="img">
                  <path data-name="layer2"
                    fill="none" stroke={this.props.wizard.color} strokeMiterlimit="10" strokeWidth="2" d="M14 31.998h36"
                    strokeLinejoin="round" strokeLinecap="round"></path>
                  <path data-name="layer1" fill="none" stroke={this.props.wizard.color} strokeMiterlimit="10"
                    strokeWidth="2" d="M28 18L14 32l14 14" strokeLinejoin="round" strokeLinecap="round"></path>
                </svg>
              </div>
            </Col>
          </Row>
          <Row className="text-center mb-3">
            <Col>
              <h1>{this.state.step.title}</h1>
              <h5>{this.state.step.subtitle}</h5>
            </Col>
          </Row>
          <Row>
            { this.state.step.options.length ? (
              <>
                {this.state.step.options.map((step, key) => {
                  return (
                    <Col md={3} xs={6} key={step.id} className="mb-3">
                      {step.options && step.options.length ? (
                      <Card className="h-100 cursor-pointer" style={step.styles} onClick={() => { this.setOption(step) }}>
                        <CardImg top className="img-fluid" src={step.image} />
                        <CardBody>
                          <CardTitle className="h4 mt-0">{step.title}</CardTitle>
                          <CardText dangerouslySetInnerHTML={{ __html: step.content ? step.content.replace(/(<? *script)/gi, 'illegalscript') : '' }}>
                          </CardText>
                        </CardBody>
                      </Card>
                      ) : this.renderComponent(step) }
                    </Col>
                  );
                })}
                { this.hasAnySubSteps() ? (
                <Row className="justify-content-center text-center mt-3 actions">
                  <Col md={3} xs={12}>
                    <button disabled={!this.state.option} onClick={() => this.nextStep()} className="btn">Continuar</button>
                  </Col>
                </Row>
                ) : undefined }
              </>
              ) : 
              this.renderComponent(this.state.step)
            }
          </Row>
        </Container>
      </Wrapper>
      ) : null
    );
  }
}

Step.propTypes = {
  wizard: PropTypes.object,
  css: PropTypes.string,
  error: PropTypes.any,
  history: PropTypes.object,
};

const mapStateToProps = state => {
  const { loading, id, wizard, css, error } = state.Wizard;
  return {
    loading, id, wizard, css, error
  };
};

export default connect(mapStateToProps, {
  getWizard
})(withRouter(Step));